.findContent{
    height: 2.8rem;
    border-top: .01rem solid #e6e6e6;
    padding: .2rem .25rem 0;
}
.findFlex{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.findLeft{
    float: left;
}
.findLeft h3{
    font-size: .32rem;
    color: #383838;
    line-height: .5rem;
}
.findLeft p{
    font-size: .24rem;
    color: #9b9b9b;
}
.find_a{
    display: flex;
    align-items: center;
}
.find_a span{
    font-size: .22rem;
    color: #929292;
    line-height: .4rem;
}
.find_a img{
    width: .8rem;
    height: .8rem;
    margin-top: .1rem;
    margin-right: .2rem;
}
.findRight{
    float: right;
}
.findRight img{
    width: 1.6rem;
    height: 1.6rem;
    margin-left: .1rem;
}