.footer{
    position: fixed;
    bottom:0;
    left: 0;
    width: 100%;
    height: 1rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-top: 1px solid #efefef;
    background-color: white;
}
.footer a{
    text-align: center;
}
.footer span{
    font-size:.5rem;
    color: #3d3d3d;
}
.footer p{
    color: #5f5f5f;
    font-size:.2rem;
}
.footer a:hover span{
    color: #23b2c3;
}
.footer a:hover p{
    color: #23b2c3;
}
.footerColor{
    color: #23b2c3;
}
.footer a:nth-child(3){
    width: 1.2rem;
    height: .7rem;
    background-color: #23b2c3;
    display: block;
    border-radius: .05rem;
    text-align: center;
}
.footer a:nth-child(3) span{
    line-height: .7rem;
    color: white;
}
 .footer a:nth-child(3) span:hover{
    color: white;
}