.header{
    height: 1rem;
    border-bottom:1px solid #e6e6e6;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.header_second{
    color: #3e3e3e;
    font-size: .3rem;
}
.header_third{
    text-align: center;
}
.header_third span{
    display: block;
    margin:.1rem 0;
    color: #646464;
    font-size: .2rem;
}
.header_first img{
    width: .3rem;
    height: .5rem;
}