.content{
    margin:0 .2rem;
}
.content img{
    width: 100%;
    height: 4rem;
    margin:.2rem 0;
}
.content p{
    font-size: .28rem;
    color: #353535;
    line-height: .5rem;
}
.share{
    margin-top: .4rem;
    display: flex;
    justify-content: space-between;
}
.home_share a{
    font-size: .18rem;
    color: #a3a3a3;
}
.icon{
    position: relative;
}
.icon span{
    font-size: .4rem;
    color: #979797;
    display: inline-block;
    margin-left: .3rem;
}
.icon i{
    font-size: .24rem;
    color: #979797;
    font-style: normal;
    position: absolute;
    top:-.05rem;
}