.loginImg{
    width: 1.4rem;
    height: 1.4rem;
    margin:1.4rem auto 0;
    display: block;
}
.goback{
    display: block;
    text-align: center;
    color: #aaaaaa;
    font-size: .24rem;
    margin-top: .36rem;
}
.third{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 1rem 0;
}
.third div{
    width: 1.7rem;
    height: .03rem;
    background-color: #f3f3f3;
}
.third span{
    font-size: .26rem;
    color: #a0a0a0;
    display: inline-block;
    margin:0 .15rem;
}
.methods{
    display: flex;
    justify-content:center;
}
.methods div{
    margin:0 .6rem;
}
.methods div img{
    width: 1.4rem;
    height: 1.4rem;
}