.userProfile{
    display: flex;
    align-items: center;
    width: 100%;
    height: 1.5rem;
    border-top:.2rem solid #f7f7f7;
    border-bottom:.2rem solid #f7f7f7;
}
.userProfile img{
    width: 1rem;
    height: 1rem;
    margin:0 .35rem;
}
.userProfile a{
    font-size: .3rem;
    color: #525252;
    display: block;
}
.userIcon{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: flex-start;
    border-bottom: .2rem solid #f7f7f7;
}
.userIcon a{
    width: 33.33%;
    height: 1rem;
    display: flex;
    align-items: center;
    box-sizing: border-box;
    border-bottom: .01rem solid #e9e9e9;
    border-right: .01rem solid #e9e9e9;
}
.userIcon a img{
    width: .47rem;
    height: .42rem;
    margin-left: .25rem;
    margin-right: .2rem;
}
.userIcon a span{
    font-size: .26rem;
    color: #404040;
}
.cellBox{
    border-bottom:4rem solid #f7f7f7;
}