
.detialProfile{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 1.3rem;
    border-bottom: .01rem solid #e6e6e6;
    padding: 0 .3rem;
}
.detailLeft{
    display: flex;
    align-items: center;
}
.detailLeft img{
    width: .7rem;
    height: .7rem;
    margin-right: .15rem;
}
.detailWord h3{
    font-size: .28rem;
    color: #3e3e3e;
    line-height: .5rem;
}
.detailWord span{
    font-size: .20rem;
    color: #a4a4a4;
}
.detailContent{
    padding: 0 .3rem;
}
.dc_h3{
    font-size: .34rem;
    color: #414141;
    margin:.3rem 0;
}
.dc_p{
    margin-bottom: 1rem;
}
.dc_p span{
    display: block;
    font-size: .26rem;
    color: #303030;
    line-height: .4rem;
    text-indent: .52rem;
}
.dc_p img{
    width: 100%;
    height: 4rem;
}
.detailFooter{
    width: 100%;
    height: 1rem;
    background-color: #fafafa;
    border-top: .01rem solid #e9e9e9;
    position: fixed;
    bottom: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.detailFooter input{
    width: 3rem;
    height: .6rem;
    border:.01rem solid #989898;
    outline: none;
    font-size: .28rem;
    color: #767676;
    padding-left: .2rem;
    border-radius: .1rem;
}
.detailFooter span{
    font-size: .56rem;
    color: #9b9b9b;
}
.detailFooter span i{
    font-size: .23rem;
    color: #969696;
    font-style: normal;
    position: absolute;
    right: 2rem;
}
.detailComment{
    margin-bottom: 2rem;
}
.detailComment h4{
    width:2rem;
    height: .7rem;
    font-size: .3rem;
    color: #3e3e3e;
    border-bottom: .05rem solid #3f3f3f;
    text-align: center;
    line-height: .7rem;
    margin-bottom: .4rem;
}
.dc_flex{
    display: flex;
    justify-content: space-between;
}
.dcl_left{
    display: flex;
    align-items: center;
}
.dcl_left img{
    width:.7rem;
    height: .7rem;
    margin-right: .2rem;
}
.dcl_content p{
    font-size: .24rem;
    color: #636363;
    line-height: .4rem;
}
.dcl_right{
    font-size: .24rem;
    color: #9b9b9b;
}
.dc_bottom{
    float: right;
}
.dc_bottom a{
    margin-left: .4rem;
}
.dc_bottom img{
    width:.3rem;
    height: .3rem;
    margin-right: .1rem;
}
.dc_bottom span{
    font-size: .24rem;
    color: #a0a0a0;
}